import { useState, useEffect } from 'react';

const wordList = `the, to, i, and, of, he, was, you, her, not, it, in, she, his, that, is, my, with, me, had, on, as, for, but, at, him, have, do, be, what, would, out, said, up, they, we, this, from, did, are, so, could, were, all, if, back, like, one, there, no, into, will, just, when, about, then, them, know, been, am, your, over, down, an, or, time, now, eyes, by, more, get, how, can, who, their, before, around, way, even, going, head, see, us, here, right, off, only, want, through, looked, hand, go, think, some, again, too, away, still, something, than, face, other, never, after, asked, thought, man, good, well, two, where, let, look, made, much, why, because, knew, got, little, door, our, any, room, come, make, take, long, first, say, its, felt, wanted, took, turned, need, hands, tell, really, sure, against, voice, should, has, left, which, very, people, told, came, another, while, last, life, anything, few, body, night, cannot, nothing, behind, being, enough, went, feel, side, does, thing, day, might, saw, until, things, though, those, yes, maybe, put, own, find, ever, every, looking, once, hair, moment, both, love, always, mind, next, place, inside, hard, help, front, without, found, house, mouth, old, same, keep, most, everything, himself, someone, toward, home, open, woman, trying, heard, pulled, arms, better, each, between, new, give, seemed, smile, work`.split(', ');

function shuffleWords(words, count = 50) {
  const shuffled = [...words].sort(() => 0.5 - Math.random());
  return shuffled.slice(0, count).join(' ');
}

export default function Home() {
  const [text, setText] = useState('');
  const [userInput, setUserInput] = useState('');
  const [timeLeft, setTimeLeft] = useState(0);
  const [wpm, setWpm] = useState(0);
  const [timerRunning, setTimerRunning] = useState(false);

  useEffect(() => {
    setText(shuffleWords(wordList));
  }, []);

  useEffect(() => {
    if (timerRunning && timeLeft > 0) {
      const interval = setInterval(() => {
        setTimeLeft((t) => t - 1);
      }, 1000);
      return () => clearInterval(interval);
    }
    if (timeLeft === 0 && timerRunning) {
      setTimerRunning(false);
      calculateWPM();
    }
  }, [timeLeft, timerRunning]);

  const startGame = (duration) => {
    setText(shuffleWords(wordList));
    setUserInput('');
    setTimeLeft(duration);
    setWpm(0);
    setTimerRunning(true);
  };

  const calculateWPM = () => {
    const wordsTyped = userInput.trim().split(/\s+/).length;
    const minutes = (timeLeft === 0 ? 1 : timeLeft) / 60;
    const wpm = wordsTyped / (1 - timeLeft / (timeLeft + 1)); // 避免除以 0
    setWpm(Math.round(wpm));
  };

  return (
    <div className="min-h-screen bg-gray-100 flex flex-col items-center justify-center p-4">
      <h1 className="text-4xl font-bold mb-6">🧠 打字練習遊戲</h1>

      <div className="mb-4 space-x-2">
        {[15, 30, 60].map((sec) => (
          <button
            key={sec}
            onClick={() => startGame(sec)}
            className="bg-blue-600 hover:bg-blue-700 text-white px-4 py-2 rounded"
          >
            {sec} 秒
          </button>
        ))}
      </div>

      <div className="bg-white shadow-md rounded p-6 w-full max-w-3xl mb-4">
        <p className="text-gray-800 mb-4 leading-relaxed">{text}</p>
        <textarea
          className="w-full h-32 border border-gray-300 rounded p-2 focus:outline-none focus:ring-2 focus:ring-blue-400"
          placeholder="開始打字..."
          value={userInput}
          onChange={(e) => setUserInput(e.target.value)}
          disabled={!timerRunning}
        />
      </div>

      <div className="text-lg">
        ⏱ 剩餘時間：<span className="font-semibold">{timeLeft}</span> 秒
        &nbsp; | &nbsp;
        ✍️ 打字速度：<span className="font-semibold">{wpm}</span> WPM
      </div>
    </div>
  );
}
